$color-black: #272727;
$color-blue: #72AEC8;
$color-gray: #EDF1F3;

html {
    font-size: 62.5%; }

body {
    font-family: "Lato", sans-serif; }

.header {
    // max-width: 192rem
    padding: 1.4rem 31rem;
    display: grid;
    grid-template-columns: 50% 1fr 1fr;
    align-items: center;
    &__icons {
        display: flex;
        gap: 2rem;
        justify-self: end; } }

.navigation {
    display: flex;
    list-style: none;
    align-items: center;
    &__item {
        font-size: 1.4rem;
        text-transform: uppercase;
        &:not(:last-child) {
            margin-right: 4.4rem; } }
    &__link {
        // letter-spacing: .1rem
        text-decoration: none;
        &:active, &:visited {
            color: $color-black; }
        &:hover {
            color: $color-blue; } } }


.main-section {
    &__body {
        background: $color-gray; } }



